.projects-section {
    padding: 50px 0;
    background-color: #7F0909;
    color: #ffd700;
    text-align: center;
    text-decoration: #ffd700;
  
}
.project-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 20px;
    
}

.project {
    position: relative;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(255, 255, 255, 0.2);
    transition: transform 0.3s ease;
}

.project:hover {
    transform: translateY(-5px) scale(1.05);
    box-shadow: 0 0 40px rgba(255, 255, 255, 0.4);
}

.project-image {
    height: 200px;
    max-width: 100%;
    background-size: cover;
    background-position: center;
}

.project-info {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    padding: 20px;
    opacity: 0;
    transition: opacity 0.3s ease;
    box-sizing: border-box;
}

.project:hover .project-info {
    opacity: 1;
}

.project h3 {
    margin-bottom: 10px;
    color: #ffd700;
}

.project-details {
    margin-top: 10px;
}

.project.expanded .project-details {
    display: block;
}

.project.expanded {
    height: auto;
    overflow: visible;
    position: relative;
}